.popup-window-container {
  position: absolute;
  z-index: 4;
  padding: 10px;

  touch-action: auto !important;

  margin-left: -15px;
  margin-top: -25px;
}

.popup-movable-header {
  cursor: -webkit-grab;
  cursor: grab;
}

.popup-movable-header:active {
  cursor: -webkit-grabbing;
  cursor: grabbing;
}