.window-centered {
  width: 70%;
  max-width: 40em;
  margin: 0 auto;
  position: relative;
  min-height: 100%;
  height: auto;

  padding: 20px;
}

@media screen and (max-width: 865px) {
  .window-centered {
      width: 100%;

      padding: 0;
      padding-top: 10px;
  }
}

.scan-lines {
  z-index:99999;

  opacity: 0.7;
  filter: alpha(opacity=70);

  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;

  width: 100%;
  height: 100%;

  overflow: hidden;
  pointer-events: none;
  background-image: radial-gradient( ellipse at center,transparent 0%,transparent 60%,rgba(0,0,0,0.25) 100% ),repeating-linear-gradient( 0deg,transparent 0px,transparent 1px,rgba(0,0,0,0.35) 3px );
  background-size: 100% 100%,100% 6px;
  -webkit-animation: flicker 300ms linear infinite;
          animation: flicker 300ms linear infinite;
}

@-webkit-keyframes flicker {
  50% {
    opacity: 0.55;
    filter: alpha(opacity=55);
  }
}

@keyframes flicker {
  50% {
    opacity: 0.55;
    filter: alpha(opacity=55);
  }
}